@import url('https://fonts.googleapis.com/css2?family=Overpass&display=swap');

.App {
  text-align: center;
  justify-content: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.noselect {

  user-select: none;
  text-decoration: none;
}


/* Hide scrollbar for Chrome, Safari and Opera */
/* html::-webkit-scrollbar {
  display: none;
} */

/* Hide scrollbar for IE, Edge, and Firefox */
/* html {
  -ms-overflow-style: none;  /* IE and Edge */
 /* scrollbar-width: none;  /* Firefox 
}  */


::-webkit-scrollbar {

  width: 10px;
  background-color: #201e1e;
}

::webkit-scrollbar-track {

  background-color: #201e1e;

}

::-webkit-scrollbar-thumb {

  height: 2px;
  width: 8px;
  border-radius: 10px;
  border: 2px solid #201e1e;
  background: #555;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #48a5be;
}

/* For IE and Edge */
* {
  -ms-overflow-style: auto;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

a {

  color: #61dafb;

}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #141313;
}

.nav-heading a {
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  font-size: 25px;
  text-decoration: none;
  position:relative;

}

.canvas-container {

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;

}

.canvas-container canvas {
  width: 100%;
  height: 100%;
}

.navigation {

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  font-family: 'Overpass', sans-serif;
  height: 68px;
  background-color: #201e1e;
  color: #61dafb;
  position: sticky;
  text-align: center;
  top: 0;
  z-index: 100;

}

.projects {

  display:grid;
  grid-template-columns: repeat(3, 0.3fr);
  overflow:hidden;
  gap: 40px;
  align-items: start;
  justify-content: center;
  margin: 0 auto;
  padding: 10px;
  
}

.project1, .project2, .project3, .project4, .project5, .project6 {

 background-color:  rgba(32, 31, 31, 0.6);
  border-radius: 15px;
  height: 95%;

}


@media (min-width: 1200px) {

  .nav-heading {

    display: flex;
    text-align: center;
    margin-right: 80px;
    font-family: 'Overpass', sans-serif;
    width: fit-content;
    font-size: 15px;
    position:relative;
  
  }

}


@media (max-width: 1200px) {

  .projects {
    grid-template-columns: repeat(2, 1fr);
    overflow:hidden;
    gap: 10px;
  }

  .heading {

    display: flex;
    justify-content: center;
    align-items: center;
    color: #61dafb;
    font-family: 'Overpass', sans-serif;
    padding: auto;
    margin: 0 auto;

  }

  .navigation {
    flex-direction: column;
    justify-content: center;
    height: auto;
    width:100%;
    margin: 0 auto;
  }

  .nav-heading a {
    justify-content: center;
    margin: 0 auto;
    text-align:center;
    font-family: 'Overpass', sans-serif;
    position:relative;

  }

  .profile {

    align-items: center;
    justify-content: center;
    margin: 0 auto 10px;

  }

  .profile-container {

    flex-direction: column;
    margin: 0 auto;
    justify-content: center;

  }

  .picture {

    margin: 0 auto;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    flex-direction: column;


  }

  .Icons {

    flex-direction: row;
    margin: 0 10px;

  }

  .Project_card img {

    margin: 0 auto;
  }

  .stack-desc {

    font-size: 10px;
   }

  .stack img{

    height: 50px;
    width:  50px;
    margin:0 auto;

  }

 .contactForm, .contactForm input, .contactForm textarea{
    width: fit-content;
    max-width: 80%;

 }

 .contactForm .error-container
 {

  font-size:14px;
  max-width: 80%;
 }

}

.nav-heading {

  display: flex;
  text-align: center;
  font-family: 'Overpass', sans-serif;
  width: fit-content;
  font-size: 15px;
  position:relative;
  

}

.nav-logo-heading {

  display: flex;
  margin-left: 80px;
  font-family: 'Overpass', sans-serif;
  width: fit-content;
  justify-content: flex-start;

}


.nav-item {

  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  justify-content: flex-start;
}

#navigation-line {

  height: 2px;
  background-color: #141313;
  box-shadow: #141313 0px 1px 10px;

}

#description {

  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  justify-content: center;
  align-items: center;
  border: 2px solid transparent;
  user-select: none;

}

.contact-desc {

    color: #61dafb;
    font-family: 'Overpass', sans-serif;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    margin: 10px auto;
    text-align: center;
    line-height: 1.5;
    padding: 5px;
    
}

.stack {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px auto;
  height: fit-content;
  width: 80%;
}

.stack-desc-container {

   background-color:  rgba(32, 31, 31, 0.5);
   border-radius: 15px;
   padding: 10px;

}

.contact-desc-container {
  background-color:  rgba(32, 31, 31, 0.6);
  border-radius: 15px;
  padding: 5px;
  margin: 3%;
}

.stack-desc {

  font-size: 25px;
  display: flex;
  max-width: fit-content;
  justify-content: center;
  align-items: center;
  line-height: 1.5;
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  margin: 10px;
  text-align: center;
  
}

.stack-desc h1 {

  font-size: 25px;
  line-height: 1.5;
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  margin: 0 20%;
  margin-bottom: 10px;
  width: 95%;

}

.stack-img-container {

  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 10px auto;
}

.stack-img-row {

  margin: 10px;
  color: #61dafb;

}

.stack img {

  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 80px;
  margin:auto;
}

@media (max-width: 768px) {

.stack-img-container {

  width:fit-content;
  height:auto;
  margin: 0 10%;
  padding: 10px;

}

.stack img {

  height: 50%;
  width: 50%;
  margin:auto;
}

.Project_card img {

  margin: 0 auto;
}


}

@media (max-width: 480px) {

  .stack img {
  
    height: 40px;
    width: 40px;
    margin:auto;
  }

  .stack-desc {

    font-size: 16px;
   }

   .profile-container {
    margin: 0 auto;
    padding: 10px;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    width: 100%; 
    box-sizing: border-box; 
  }

  .profile_Desc {

    font-size: 18px;
    max-width: 70%;
   
  }

  .Project_card img {

    width: 95%;
    height: 100%;
    margin: 0 auto;
  }

}

.profile-container {
  display: flex;
  justify-content: center;
  padding: 10px;
  align-items: center;
  width: 65%;
  margin: 10px auto;
}


.profile {

  width: 180px;
  height: 180px;
  display: flex;
  align-items: center;
  border-radius: 75%;
  justify-content: center;
  margin: 0px auto 0px;
  border: 2px solid transparent;
  object-fit:cover;

}

.profile_Desc {

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 23px;
  line-height: 1.35;
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  max-width: 60%;
}

.heading {

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  margin: 0 auto;
  text-align: center;
  position:relative;
  width:fit-content;
}

.heading::after,
.nav-heading::after {

  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  border-bottom: 0.5px solid;
  border-color: #61dafb;
  transition: width 0.5s;

}

.heading:hover::after,
.nav-heading:hover::after {

  width: 100%;

}

.picture {

  margin: 10px auto;
  justify-content: center;
  border: 2px solid transparent;


}

.Project_card {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 20px;
  align-items: center;
  overflow: hidden;
  
}

.Project_card #heading{

  margin: 10px;

}


@media (max-width: 768px) {
  .projects {
    grid-template-columns: repeat(1, 0.6fr); 
  }

  .heading {

    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    color: #61dafb;
    font-family: 'Overpass', sans-serif;
    margin: 0 15%;
    position:relative;
  }

  .profile_Desc {

    font-size: 20px;
    max-width: 100%;

  }

  .profile {

    margin: 0 auto 30px;

  }
}

@media (max-width: 480px) {

   .socials {

    margin: 0 auto;
   }

  .projects {
    grid-template-columns: 1fr;
  }

  .profile_Desc {

    font-size: 16px;
    max-width: 100%;

  }
 
  .profile {

    margin: 0 auto 50px;

  }

  .nav-heading a {

    justify-content: center;
    text-align:center;
    margin: 0 auto;
    font-size: 18px;
    font-family: 'Overpass', sans-serif;
    position:relative;

  }

  .Project_card img {

    width: 380px;
    height: auto;

  }



.heading {

  margin: 0 auto;
}

 .contact-desc {
    font-size: 18px;
    margin: 0 auto;
    text-align: center;
    padding: 10px;
    line-height: 1.5;
 }


}


#description {

  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 20px;
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  margin: 10px;

}

#heading {

  display: flex;
  justify-content: center;
  font-size: 30px;
  padding: 10px;
  color: #61dafb;
  font-family: 'Poppins', sans-serif;
}

.Project_card img {

  height: 230px;
  border: 2px solid transparent;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.Project_card img:hover {

  border: 2px solid;
  border-color: #61dafb;
  box-shadow: #61dafb 0px 1px 10px;
  transition: 0.10s;
}

.socials {
  display: flex;
  width: fit-content;
  padding: 10px;
  border-radius: 15px;
  border: 2px solid transparent;
  align-items: center;
  justify-content: center;
}

.socials1, .socials2 {

  margin: 10px;
}

.socials img:hover,
.profile:hover {
  border: 2px solid;
  border-color: #61dafb;
  transition: 0.10s;
  box-shadow: #61dafb 0px 1px 10px;
}

.Icons:hover {

  border: 1px solid;
  border-color: #61dafb;
  transition: 0.10s;
  box-shadow: #61dafb 0px 1px 10px;

}

.contactForm {

  display: flex;
  flex: auto;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 10px auto;
  font-family: 'Overpass', sans-serif;
  color: #61dafb;
  width: 100%;
}

.contactForm input {

  margin: 10px;
  padding: 10px;
  border: 1px solid;
  border-color: #61dafb;
  border-radius: 5px;
  background-color: #201e1e;
  color: #61dafb;
  font-family: 'Overpass', sans-serif;
  font-size: 15px;

}

.contactForm button,
.Project_card button {

  display: flex;

  justify-content: center;
  align-items: center;
  outline: none;
  text-decoration: none;
  padding: 10px;
  border: 2px solid;
  border-color: #61dafb;
  background-color: #201e1e;
  border-radius: 5px;
  color: #61dafb;

}

.contactForm button:hover,
.contactForm input:hover,
.Project_card button:hover {

  box-shadow: #61dafb 0px 1px 10px;
  transition: 0.10;
  text-decoration: none;
  outline: none;

}

#img src {

  border-radius: 50%;
  margin: 10px;

}

.contactForm input {

  height: 30px;
  width: 400px;
  margin: 10px;
  padding: 10px;
  border: 1px solid;
  border-color: #61dafb;
  background-color: #201e1e;
  border-radius: 5px;
  color: #61dafb;

}

.contactForm textarea {

  height: 80px;
  width: 400px;
  margin: 10px;
  padding: 10px;
  border: 1px solid;
  border-color: #61dafb;
  background-color: #201e1e;
  font-family: 'Overpass', sans-serif;
  border-radius: 5px;
  color: #61dafb;

}

.contactForm h2 {

  font-family: 'Overpass', sans-serif;
  color: #61dafb;
  font-size: 25px;

}

.footer {

  height: fit-content;
  background-color: #201e1e;

}

.footer-item {

  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  color: #61dafb;

}

.item-link1, .item-link2 {

  flex: 0;
  padding: 10px;

}

.error-container {

  width: 440px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}

.contactForm button {

  order: 2;
  flex: 1;
  margin: 10px;
  padding: 10px;
  border: 2px solid;
  border-color: #61dafb;
  background-color: #201e1e;
  border-radius: 5px;
  color: #61dafb;

}

#error-check {

  order: 1;
  flex: 1;
  margin: 10px;
  padding: 10px;
  display: flex;
  margin: 10px auto;
  color: #fc0000;
  font-family: 'Overpass', sans-serif;

}

.back-to-top {


  background-color: #61dafb;

}